


export const _setRecommend = (valueList) =>{//处理实时公交的处理数据
    return valueList.map( item =>{
        return {
            carNum: 0,
            lineId: item.lineId,
            siteOrder: item.viewSort,
            siteRoad: 0,
            upDown: item.upDown
        }
    })

}


// 将最小元素推至最前
export const _sortView = (array) => { 
  let len = array.length,
  i, j, tmp, result;
  result = array.slice(0);
  for (i = 0; i < len; i++) {
    for (j = len - 1; j > i; j--) {
      if (result[j].lineId < result[j - 1].lineId) {
        tmp = result[j - 1];
        result[j - 1] = result[j];
        result[j] = tmp;
      }
    }
  }
  return result;
}

// 去重
export  const  _unique = (array) =>{
  if(!array.length) return []
  let n = {}, r = [], val, type;
      for (let i = 0; i < array.length; i++) {
          val = array[i].soleId;
          type = typeof val;
          if (!n[val]) {
              n[val] = [type];
              r.push(array[i]);
          } else if (n[val].indexOf(type) < 0) {
              n[val].push(type);
              r.push(array[i]);
          }
      }
  return r;
}

export const _mergeList = ( reacommendList,selectLineCarsList ) =>{//合并数组
    let viewData = reacommendList
    for (let i = 0; i < viewData.length; i++) Object.assign( viewData[i] , selectLineCarsList[i])
    return viewData
}

export const _setChildDOM = ( value )=>{
  let newArr=[]
  for (let i = 0; i < value.length; i++) {
      if(value[i].drawInTime!==-1){
        newArr.push(value[i])
      }
  }
  return newArr.sort((a,b)=> a.drawInTime - b.drawInTime)
}

export const _setCacheList = ( array ) =>{//过滤保存数据
  return array.map(item=>{
    return {
      flag:item.flag,
      soleId:item.soleId,
    }
  })
}

export const _setFlag = ( value,valueC ) =>{//查看是否收藏
  let arrayLocal=value
  let arrayCache=valueC
  for(let i=0;i<arrayLocal.length;i++){
    for(let j=0;j<arrayCache.length;j++){
      if(arrayLocal[i].soleId===arrayCache[j].soleId){
        arrayLocal[i].flag=arrayCache[j].flag
      }
    }
  }
  return arrayLocal
}


export const _delCache = ( array ,soleId) =>{
  let cacheList = array
  cacheList=cacheList.filter(item=>{
    return item.soleId!==soleId
  })
  return cacheList
}


export const _filter = ($rempm,$cache) =>{
  let $filterData=[]
  if(!$cache.length) return []
  for(let i=0;i<$rempm.length;i++){
    for (let j = 0; j < $cache.length; j++) {
      if($rempm[i].soleId===$cache[j].soleId){
        $rempm[i].flag=$cache[j].flag
        $filterData.push($rempm[i])
      }
    }
  }
  return $filterData
}

export const _getDistance = (lat1, lng1, lat2, lng2) =>{ // 根据经纬度计算两点之间的距离
  let radLat1 = lat1*Math.PI / 180.0
  let radLat2 = lat2*Math.PI / 180.0
  let a = radLat1 - radLat2
  let b = lng1*Math.PI / 180.0 - lng2*Math.PI / 180.0
  let s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a/2),2) +
  Math.cos(radLat1)*Math.cos(radLat2)*Math.pow(Math.sin(b/2),2)))
  s = s *6378.137 
  s = Math.round(s * 10000) / 10000*1000
  return Math.ceil(s)
}

export const _setLocation = (array1,array2) =>{//将行驶车辆添加到数组中
  let newBusList=array1
  for (let i = 0; i < newBusList.length; i++) {
    for (let j = 0; j < array2.length; j++) { 
      if(newBusList[i].viewSort===array2[j].nextNum){
        newBusList[i].busLocat=array2[j]
      }
    }
  }
  return newBusList
}

export function deepCopy(target){ 
  let copyed_objs = [];//此数组解决了循环引用和相同引用的问题，它存放已经递归到的目标对象 
  function _deepCopy(target){ 
      if((typeof target !== 'object')||!target){return target;}
      for(let i= 0 ;i<copyed_objs.length;i++){
          if(copyed_objs[i].target === target){
              return copyed_objs[i].copyTarget;
          }
      }
      let obj = {};
      if(Array.isArray(target)){
          obj = [];//处理target是数组的情况 
      }
      copyed_objs.push({target:target,copyTarget:obj}) 
      Object.keys(target).forEach(key=>{ 
          if(obj[key]){ return;} 
          obj[key] = _deepCopy(target[key]);
      }); 
      return obj;
  } 
  return _deepCopy(target)
}


export const _filterListView = (array) =>{//过滤车辆时间数组并排序
  let viewList = array
  viewList=viewList.filter(item=>{
    return item.drawInTime!==-1
  })
  return viewList.sort((a,b)=>a.drawInTime-b.drawInTime)
}

export const  _TIMES = (time)=>{
  let theTime = parseInt(time);// 秒
  let middle= 0;// 分
  let hour= 0;// 小时
  if(theTime > 60) {
      middle= parseInt(theTime/60);
      theTime = parseInt(theTime%60);
      if(middle> 60) {
          hour= parseInt(middle/60);
          middle= parseInt(middle%60);
      }
  }
  let result=`${parseInt(theTime)}`
  if(middle > 0) {
      result=`${parseInt(middle)<10?`0${parseInt(middle)}`:parseInt(middle)}:${result<10?`0${result}`:result}`
  }
  if(hour> 0) {
      result=`${parseInt(hour)<10?`0${parseInt(hour)}`:parseInt(hour)}:${result<10?`0${result}`:result}`
  }
  if(middle<=0&&hour<=0){
    result=`00:${result<10?`0${result}`:result}`
  }
  return result;
}
  