import React, { Component } from 'react'
import './index.scss'
export default class Busheader extends Component {
    render() {
        const {bus}=this.props
        return (
            <div className="header">
                <div className="header_box">
                    <div className="box_left">
                        <div className="left_text">{bus.beginSite} → {bus.endSite}</div>
                        <div>首末班 {bus.beginTime}--{bus.endTime}</div>
                    </div>
                    <div className="box_right">票价{bus.ticketPrice/100}元  支持换乘</div>
                </div>
            </div>
        )
    }
}
