import React, { Component } from 'react'
import {  selectLineCars } from '@/tool/api/real_bus.js'
import {connect} from 'react-redux'//引入redux容器组件
import { CITY } from '@/redux/city_location/variable.js'//引入reducers变量
import { _filterListView,_TIMES} from '@/tool/Processing.js'
import Busheader from '@/components/busHeader/index'
import Loading from '@/tool/loading/Loading.js'
import fff from '@/assets/imgs/fff.png'
import bule from '@/assets/imgs/bule.png'
import yellow from '@/assets/imgs/yellow.png'
import './index.scss'
const AMap = window.AMap;
class Destination extends Component {
    constructor(props){
        super(props)
        const meroAll=JSON.parse(props.location.state.meroAll)
        this.state={
            siteOrder:meroAll.siteOrder,
            viewList:meroAll.viewList,
            lineId:meroAll.lineId,
            upDown:meroAll.upDown,
            selectID:null,
            BUSname:null,
            destination:[],
            deskHeight: null,
        }
    }
    componentDidMount(){
        this.setState({
            deskHeight:document.body.clientHeight+'px'
        })
    }
    _changeSelect = (index,name,item)=>{//选择站点
        const { siteOrder } = this.state
        const { latitude,longitude } = item
        if(index<siteOrder) return
        this.setState({
            selectID:index,
            BUSname:name,
            destination:[parseFloat(longitude),parseFloat(latitude)]
        })
    }
    _setTime = async (lineId,siteOrder,upDown,BUSname) =>{
        Loading.show()
        let userId=Math.ceil(Math.random()*100000)
        let seachNew= [{
            carNum: 0,
            lineId: lineId,
            siteOrder:siteOrder, 
            siteRoad: 1, 
            upDown:upDown }]
        let {code , data:busList} = await selectLineCars({
            userid: userId,
        lineSiteCarsParams: seachNew})
        if( code!==200) return
        Loading.remove()
        let LISTSORT=_TIMES(_filterListView(busList[0].list)[0].drawInTime)
        sessionStorage.setItem('busId',JSON.stringify({time:LISTSORT,name:BUSname}))
        this.props.history.goBack();
    }
    _determine = ()=>{//确定
        let { BUSname,destination} = this.state//lineId,upDown,selectID
        let that=this
        if(BUSname===null) return;
        const { latitude,longitude } = this.props.mylocation//当前所在城市
        AMap.plugin('AMap.Driving', function() {
            var driving = new AMap.Driving({
              // 驾车路线规划策略，AMap.DrivingPolicy.LEAST_TIME是最快捷模式
              policy: AMap.DrivingPolicy.LEAST_TIME
            })
            let startLngLat = [longitude,latitude]
            driving.search(startLngLat, destination, function (status, result) {
              const { info } = result
              if(info==='OK'){
                sessionStorage.setItem('busId',JSON.stringify({time:_TIMES(result.routes[0].time),name:BUSname}))
                that.props.history.goBack();
              }else{
                  alert(info)
              }
            })
          })
    }
    render() {
        const { topData }= this.props.location.state
        const { viewList,siteOrder,selectID,deskHeight } = this.state
        return ( 
            <div className="ion" 
            style={{ height: deskHeight}}
            >
                <div className="icon_height">
                    <Busheader bus={ topData }></Busheader>
                    <div className="ion_con">
                        <div className="con_top">
                            <div className="top_line"></div>
                            <div className="top_text">{topData.name}</div>
                        </div>
                        <div className="con_fl">
                            {
                                viewList.map((item,index)=>{
                                    return (
                                        <div className="fl_box" key={index}>
                                            <div className="box_one"
                                            style={item.num<siteOrder?
                                                {backgroundColor: ' #BBBBBB'}
                                                :item.num===siteOrder?
                                                {backgroundColor: '#FFB311'}
                                                :{backgroundColor: '#0090FF'}
                                            }
                                            >{item.num}</div>
                                            <div className="box_two" style={{backgroundColor:item.color}}>
                                                <img src={item.num===siteOrder?yellow:item.num===selectID?bule:fff} alt="" />
                                            </div>
                                            <div className="box_three"
                                            style={item.num<siteOrder?
                                                {color: '#8E8E8E'}
                                                :item.num===siteOrder?
                                                {color: '#D88011'}:
                                                item.num===selectID?
                                                {color: '#0090FF'}
                                                :{color: '#3A3A3A'}
                                            }
                                            onClick={()=>this._changeSelect(item.num,item.name,item)}
                                            >{item.name}</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
                <div className="con_fixed" onClick={()=>this._determine()}>确定</div>
            </div>
        )
    }
}

const mapStateToProps=(state)=>({mylocation:state.locationCity,myLineData:state.recommendData})
const mapDisPatchToProps=(dispatch)=>({
    _changeCity:(number)=>{
        dispatch({type:CITY,data:number})
    }
})


export default connect(mapStateToProps,mapDisPatchToProps)( Destination )